.AboutPage{
    padding: 0px 0px 50px 0px;
    scroll-snap-type: y;
}
.about-section{
    padding: 0px 10%;
    align-content: center;
}

.about-content p{
    padding-bottom: 10px;
    background-color: var(--scnd-color);
}

.abouthero{
    width: 40vw;
    height: auto;
    object-fit: contain;
    padding: 0% 3% 1% 0%;
    float: left; 
    border-radius: 20% 0%;
    transform: translateY(-15%);
}
.about-section p{
    text-align: left; 
}
.about-title{
    font-family: var(--display-ft);
    font-size: 250%;
    padding-bottom: 2px;
    margin: 0px 35% 3% 35%; 
    border-radius: 5%;
    padding-top: 30px;
}
.about-content{
    line-height: 25px;
    font-family: Helvetica, sans-serif;
}

.skills-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding-left: 10%;
    font-family: var(--main-ft);
}
.skill-object{
    display: flex;
    padding: 0px 0px 15px 0px;
}

.skill-object p{
    padding-left: 5px;
}
.social-icon{
    color: var(--scnd-color); 
    font-size: 2em;
    padding: 0px 20px;
}
.resume{
    padding: 20px 50px 10px 10px
}
.resume-btn{
    margin-top: 2%;
}
.coding-img{
    width: 25%;
    height: auto;
    padding-bottom: 2%;
}

/* ----------------------------------------------------- */

@media only screen and (max-width: 750px) {
   .about-section {
        display: grid;
        grid-template-columns: 1fr;
    }
    .abouthero{
        justify-self: center; 
    }
    .skills-container{
        grid-template-columns: 1fr 1fr;
        padding-left: 15%;
    }
    .view-resume {
        display: flex;
        justify-content: center;
    }
    .about-title{
        font-size: 200%;
        margin: 0px 10% 3% 10%; 
        padding-top: 0px;
    }
  }
  @media only screen and (max-width: 1375px) {
    .view-resume {
        display: flex;
        justify-content: center;
    }
  }